@import"https://fonts.googleapis.com/css2?family=Cinzel:wght@400;600;700&family=Noto+Serif+SC:wght@400;500;700&display=swap";.shell{--ink: #e8ddc6;--ink-muted: #b7a586;--line: #6f5632;--line-soft: #4f3b23;--panel: rgba(16, 12, 9, 0.9);--panel-2: rgba(34, 24, 16, 0.9);--gold: #b49a63;width:min(96vw,1920px);max-width:none;min-height:96vh;margin:0 auto;padding:14px;display:flex;flex-direction:column;gap:10px;color:var(--ink);background:radial-gradient(circle at 14% -10%, rgba(188, 146, 71, 0.2), transparent 34%),radial-gradient(circle at 90% 0, rgba(130, 40, 40, 0.18), transparent 28%),linear-gradient(150deg, #080705, #120d09 48%, #090705);border:1px solid var(--line);border-radius:14px;font-family:"Noto Serif SC","Source Han Serif SC",serif;position:relative;overflow:hidden;box-shadow:0 24px 70px rgba(0,0,0,.5),inset 0 0 0 1px rgba(255,223,160,.06);text-align:center}.shell::before,.shell::after{content:"";position:absolute;inset:0;pointer-events:none}.shell::before{background:linear-gradient(transparent 97%, rgba(232, 193, 110, 0.05)),linear-gradient(90deg, transparent 97%, rgba(166, 45, 45, 0.03));background-size:100% 4px,4px 100%;mix-blend-mode:soft-light}.shell::after{border:1px solid rgba(188,156,98,.24);border-radius:11px;margin:6px}.start-screen{min-height:calc(100vh - 110px);display:grid;place-items:center;border:1px solid var(--line);border-radius:12px;position:relative;overflow:hidden;background:radial-gradient(circle at 20% 25%, rgba(118, 34, 34, 0.3), transparent 42%),radial-gradient(circle at 75% 15%, rgba(168, 134, 69, 0.25), transparent 34%),linear-gradient(140deg, rgba(12, 9, 7, 0.94), rgba(19, 13, 9, 0.95))}.start-screen__veil{position:absolute;inset:0;background:repeating-linear-gradient(0deg, transparent 0, transparent 5px, rgba(232, 193, 110, 0.05) 5px, rgba(232, 193, 110, 0.05) 6px);animation:pageDrift 16s linear infinite}.start-card{position:relative;width:min(1600px,100% - 28px);border:1px solid #8f7044;border-radius:10px;background:linear-gradient(180deg, rgba(20, 14, 10, 0.95), rgba(13, 10, 8, 0.96));padding:28px 26px;box-shadow:0 18px 44px rgba(0,0,0,.44),inset 0 0 0 1px rgba(217,181,113,.1);animation:cardLift 520ms ease-out}.start-card__kicker{margin:0;font:600 11px/1.2 "Cinzel","Noto Serif SC",serif;letter-spacing:.18em;color:#c9b086}.start-card h2{margin:10px 0 8px;font:700 clamp(72px, 12vw, 168px)/1.04 "Cinzel","Noto Serif SC",serif;letter-spacing:.12em;color:rgba(0,0,0,0);background:linear-gradient(180deg, #fff6dc 0%, #f8de9f 24%, #d3aa57 52%, #f1d188 74%, #8d6428 100%);-webkit-background-clip:text;background-clip:text;-webkit-text-fill-color:rgba(0,0,0,0);text-shadow:0 0 6px rgba(181,141,64,.26),0 0 14px rgba(116,82,30,.22),0 0 26px rgba(64,44,16,.16)}.start-card__desc{margin:0;color:var(--ink-muted);line-height:1.65}.start-card__author{margin:0 0 10px;color:#ceb893;letter-spacing:.05em}.start-card__quote{margin:14px 0 0;padding-left:0;border-left:0;color:#d7c7a7;opacity:.92}.start-card__mode{margin:8px 0 0;color:#ceb893;font-size:13px}.start-card__actions{margin-top:18px;display:grid;grid-template-columns:1fr;gap:10px;width:min(420px,92%);margin-inline:auto;justify-items:center;align-items:center}.start-card__actions button{width:min(320px,100%);display:block;text-align:center;margin-inline:auto}.start-card__actions .xeno{border-color:#8b4a4a;background:linear-gradient(180deg, #5e2a2a, #3e1b1b)}.create-card{display:grid;gap:14px}.create-card h2{margin:0;font:700 30px/1.2 "Cinzel","Noto Serif SC",serif;text-align:center;color:#f3e4c4;text-shadow:0 0 10px rgba(160,120,58,.22)}.create-card label{display:grid;gap:8px;padding:12px;border:1px solid rgba(190,152,85,.5);border-radius:10px;background:linear-gradient(180deg, rgba(227, 183, 107, 0.05), rgba(227, 183, 107, 0)),rgba(25,18,12,.86);box-shadow:inset 0 0 0 1px rgba(255,223,163,.06)}.create-card label>span{font-size:18px;font-weight:700;letter-spacing:.03em;color:#f0ddb7}.create-card input,.create-card select,.create-card textarea{font-size:18px;color:#f5e8cc;background:rgba(31,22,14,.95);border-color:#b4915e}.desc-field textarea{min-height:180px;font-size:14px;line-height:1.5;white-space:pre-wrap}.xeno-box{border:1px solid rgba(156,79,79,.55);border-radius:10px;padding:10px;background:linear-gradient(180deg, rgba(120, 42, 42, 0.16), rgba(40, 16, 16, 0.2));display:grid;gap:8px}.xeno-box>p{margin:0}.attr-list{display:grid;gap:8px}.attr-item{border:1px solid rgba(190,152,85,.38);border-radius:8px;background:rgba(26,18,12,.72);padding:10px;display:grid;gap:8px}.attr-item header{display:grid;gap:3px}.attr-item header strong{font-size:18px;color:#f0ddb7}.attr-item header span{font-size:13px;color:var(--ink-muted)}.attr-ctrl{display:flex;justify-content:center;align-items:center;gap:8px}.attr-ctrl b{min-width:28px;font-size:20px;color:#f4e7cd}.topbar,.row{display:flex;justify-content:center;align-items:center;gap:8px;flex-wrap:wrap}.center-row{justify-content:center}.title h1{margin:0;font:700 24px/1.2 "Cinzel","Noto Serif SC",serif;letter-spacing:.05em}.title p{margin:3px 0 0;font-size:12px;color:var(--ink-muted)}.controls{display:flex;gap:8px;justify-content:center}.db-btn{border-color:#8f7044;background:linear-gradient(180deg, rgba(103, 78, 41, 0.45), rgba(49, 36, 19, 0.45))}.panel{border:1px solid var(--line-soft);border-radius:10px;background:linear-gradient(180deg, rgba(255, 230, 180, 0.03), transparent),var(--panel);padding:10px;box-shadow:inset 0 0 0 1px rgba(255,223,160,.05)}.main-layout{display:grid;grid-template-columns:260px minmax(0, 1fr);gap:10px;align-items:start}.main-column{display:grid;gap:10px}.player-sidebar{position:sticky;top:10px;text-align:left}.player-sidebar h3{margin:0 0 10px;font:700 18px/1.2 "Cinzel","Noto Serif SC",serif;letter-spacing:.04em;color:#f2dfbb}.player-sidebar dl{margin:0;display:grid;gap:8px}.player-sidebar dl>div{display:grid;gap:4px;padding:8px;border:1px solid rgba(190,152,85,.38);border-radius:8px;background:rgba(26,18,12,.72)}.player-sidebar dt{font-size:12px;color:var(--ink-muted)}.player-sidebar dd{margin:0;font-size:16px;color:#f4e7cd}.inventory-panel{margin-top:10px;border:1px solid rgba(190,152,85,.35);border-radius:8px;padding:8px;background:rgba(22,16,11,.72);display:grid;gap:8px}.inventory-panel h4{margin:0;color:#efdcb8}.inventory-form{display:grid;grid-template-columns:1fr;gap:6px}.inventory-item{border:1px solid rgba(190,152,85,.28);border-radius:8px;padding:7px;background:rgba(29,21,14,.72);text-align:left}.inventory-item header{display:flex;justify-content:space-between;align-items:center;gap:6px}.inventory-item p{margin:4px 0 0}.attr-card dd{display:flex;gap:6px;flex-wrap:wrap}.attr-chip{border:1px solid rgba(190,152,85,.35);border-radius:999px;padding:2px 7px;font-size:12px;color:#e8d8b7;background:rgba(20,14,10,.7)}.settings{display:grid;grid-template-columns:1fr 1fr;gap:8px}.settings label:last-of-type{grid-column:1/-1}.settings label.full{grid-column:1/-1}.toggle{grid-column:1/-1;display:flex;align-items:center;gap:10px;font-size:12px;color:var(--ink-muted);justify-content:center}.toggle input[type=checkbox]{appearance:none;-webkit-appearance:none;width:44px;height:24px;border-radius:999px;border:1px solid #8e6c3d;background:linear-gradient(180deg, #2c2014, #1c150e);position:relative;cursor:pointer;transition:background .2s ease,border-color .2s ease;flex:0 0 auto}.memory-vault{display:grid;gap:8px}.memory-item{border:1px solid rgba(190,152,85,.34);border-radius:8px;background:rgba(23,16,11,.82);padding:8px;text-align:left}.memory-item header{display:flex;justify-content:space-between;gap:8px;align-items:baseline;margin-bottom:6px}.memory-item pre{margin:0;white-space:pre-wrap;line-height:1.45;color:#e6d7b8}.memory-item.mega{border-color:rgba(118,88,42,.6);background:rgba(30,22,13,.86)}.npc-panel{display:grid;gap:8px}.npc-form{display:grid;grid-template-columns:repeat(3, minmax(0, 1fr));gap:8px}.npc-columns{display:grid;grid-template-columns:repeat(2, minmax(0, 1fr));gap:8px}.npc-columns section{border:1px solid rgba(190,152,85,.3);border-radius:8px;padding:8px;background:rgba(23,16,11,.7);text-align:left}.npc-columns h4{margin:0 0 6px;color:#ead9b8}.npc-card{border:1px solid rgba(190,152,85,.28);border-radius:8px;padding:8px;margin-bottom:6px;background:rgba(31,22,14,.72)}.npc-card header{display:flex;justify-content:space-between;align-items:center;gap:6px}.npc-card p{margin:4px 0 0}.toggle input[type=checkbox]::after{content:"";position:absolute;top:2px;left:2px;width:18px;height:18px;border-radius:50%;background:linear-gradient(180deg, #d2bf96, #9a7f4f);box-shadow:0 1px 4px rgba(0,0,0,.4);transition:left .2s ease}.toggle input[type=checkbox]:checked{background:linear-gradient(180deg, #6e2a2a, #4c1c1c);border-color:#b8835f}.toggle input[type=checkbox]:checked::after{left:22px}input,select,textarea,button{border:1px solid #7b6040;border-radius:7px;background:rgba(34,24,16,.9);color:var(--ink);padding:8px}input,select,textarea{width:100%}button{cursor:pointer;background:linear-gradient(180deg, #7c6038, #523a24);border-color:#ad8d59;transition:filter 160ms ease,transform 160ms ease}button:hover{filter:brightness(1.08);transform:translateY(-1px)}button:active{transform:translateY(0)}button.ghost{background:linear-gradient(180deg, #3a2b1d, #281d13);border-color:#6d5637}.quick button:nth-child(2){border-color:#8b5f43}.quick button:nth-child(3){border-color:#844444;background:linear-gradient(180deg, #6e3535, #4a2222)}button.small{font-size:11px;padding:4px 8px}button:disabled{opacity:.55;cursor:not-allowed;transform:none}.quick{display:flex;gap:8px}.chat{display:flex;flex-direction:column;gap:10px}.messages{max-height:52vh;overflow:auto;display:flex;flex-direction:column;gap:8px;padding-right:2px;align-items:center}.messages::-webkit-scrollbar{width:8px}.messages::-webkit-scrollbar-thumb{background:rgba(155,122,72,.7);border-radius:10px}.msg{border:1px solid #685133;border-radius:9px;padding:10px;background:rgba(24,18,12,.8);width:min(940px,100%)}.msg--user{border-color:#8f5d3e;background:linear-gradient(180deg, rgba(33, 22, 15, 0.95), rgba(25, 18, 13, 0.9))}.msg--assistant{border-color:#7b6a49;background:linear-gradient(180deg, rgba(20, 16, 11, 0.95), rgba(14, 12, 9, 0.92))}.msg--error{border-color:#8f3d3d;background:rgba(128,38,38,.26)}.msg header{font:600 11px/1.3 "Cinzel","Noto Serif SC",serif;letter-spacing:.08em;color:var(--ink-muted);margin-bottom:5px}.msg pre{white-space:pre-wrap;margin:0;font:inherit;line-height:1.7;text-align:center}.composer{display:flex;flex-direction:column;gap:8px}.hint{font-size:12px;color:var(--ink-muted)}.system-float{position:fixed;right:14px;top:14px;z-index:200000;display:flex;flex-direction:column;gap:8px;width:min(360px,100vw - 24px)}.system-notice{border:1px solid #9d7a46;border-radius:8px;background:linear-gradient(180deg, rgba(24, 18, 12, 0.97), rgba(18, 14, 10, 0.97));box-shadow:0 10px 30px rgba(0,0,0,.4);padding:10px}.system-notice header{display:flex;justify-content:space-between;align-items:center;gap:8px;margin-bottom:6px;color:var(--gold)}.db-notice{border-color:#b68a48;box-shadow:0 12px 30px rgba(0,0,0,.45)}@keyframes cardLift{from{opacity:0;transform:translateY(10px) scale(0.99)}to{opacity:1;transform:translateY(0) scale(1)}}@keyframes pageDrift{from{transform:translateY(0)}to{transform:translateY(6px)}}@media(max-width: 700px){.main-layout{grid-template-columns:1fr}.player-sidebar{position:static;text-align:center}.settings,.quick{grid-template-columns:1fr;display:grid}.npc-form,.npc-columns{grid-template-columns:1fr}.title h1{font-size:19px}.start-card{padding:20px 16px}.start-card__actions{display:grid;grid-template-columns:1fr}}